// dependencies
import { H, getAPI, __html, __attr, attr, getCookie, showLoader, hideLoader, initHeader, initBreadcrumbs, parseApiError, onClick, onChange, simulateClick, spaceID, toast, link } from '@kenzap/k-cloud';
import { getParam, countries, languages, initFooter, formatTime } from "../_/_helpers.js"
import { addLocaleModal } from "../_/_add_locale_modal.js"
import { editLocaleModal } from "../_/_edit_locale_modal.js"
import { HTMLContent } from "../_/_cnt_edit.js"

/**
 * Represents the EditLocale class.
 * This class handles the editing of locales and their translations.
 * 
 * @link https://translate.kenzap.cloud/edit/?ext=ecommerce&slug=ecommerce&sid=1002170#
 * @since 1.2.0
 *
 * @package Translate
 */
class EditLocale {

    constructor() {
        this.state = {
            firstLoad: true,
            locales: {},
            ext: getParam('ext'),
            slug: getParam('slug'),
            ajaxCount: 0,
            data: {},
        };

        this.init();
    }

    init = () => {
        
        this.data();
    }

    /**
     * Retrieves data from the API and performs necessary operations.
     */
    data = () => {

        // show loader during first load
        if (this.state.firstLoad) showLoader();

        // do API query
        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    locale: {
                        type:       'locale',
                        id:         getCookie('lang')
                    },
                    locales: {
                        type:       'find', 
                        key:        'locale-' + this.state.ext,
                        fields:     ['_id', 'language', 'location', 'locale', 'ext', 'updated'],
                    },
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){

                // init header
                initHeader(response);

                // get core html content 
                this.home();  

                // render table
                this.render(response);

                // listeners
                this.listeners();

                // initiate footer
                initFooter(this);

                // first load
                this.state.firstLoad = false;

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }

    /**
     * Retrieves the default locale data from a github repo.
     * @param {string} locale - The locale to retrieve data for.
     */
    getDefaultLocale = (locale) => {
        
        // console.log(locale);

        // do API query
        fetch('https://raw.githubusercontent.com/kenzap/'+this.state.ext+'/main/public/locales/'+locale+'.json', {
            method: 'get',
            headers: { }
        })
        .then(response => response.text())
        .then(response => { this.state.locales[locale] = response; this.shouldLocaleModal() })
        .catch(error => { console.error('Error:', error); });
    }

    /**
     * Renders the page with the provided response data.
     *
     * @param {Object} response - The response data containing locales.
     * @returns {void}
     */
    render = (response) => {

        // initiate breadcrumbs
        initBreadcrumbs(
            [
                { link: link('https://dashboard.kenzap.cloud?launcher=translate'), text: __html('Dashboard') },
                { link: link('/'), text: __html('Translate') },
                { text: __html('Extension') },
            ]
        );

        this.state.locales = response.locales;

        // init table header
        document.querySelector(".table thead").innerHTML = `
        <tr>
            <th>${ __html("Locale") }</th>
            <th>${ __html("Code") }</th>
            <th>${ __html("Updated") }</th>
            <th>${ __html("") }</th>
        </tr>`;

        // render table rows
        let rows = "";
        response.locales.forEach((el, i) => {
        
            let l = languages.filter((obj) => { return obj.code === el.language });
            let c = countries.filter((obj) => { return obj.code === el.location });
            let localeTitle = __html(l[0].name) + ( c[0] ? ' (' + __html(c[0].name) + ')' : '' );

            rows += `
            <tr>
                <td class="py-3">
                    <a href="#" class="edit-locale" data-title="${ localeTitle }" data-language="${ el.language }" data-id="${ el._id }">${ localeTitle }</a> ${ el.location ? '<img class="ms-2 cc-flag" src="https://cdn.kenzap.com/flag/' + el.location.toLowerCase() + '.svg" alt="location footer flag">': ''}
                </td>
                <td class="py-3">
                    ${ el.locale }
                </td>
                <td class="py-3">
                    ${ formatTime(el.updated) }
                </td>
                <td class="text-end">
                    <div class="dropdown applicationsActionsCont" data-boundary="viewport" data-bs-boundary="viewport">
                        <svg id="applicationsActions${i}" data-bs-toggle="dropdown" data-boundary="viewport" data-bs-boundary="viewport" aria-expanded="false" xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-three-dots-vertical dropdown-toggle po" viewBox="0 0 16 16">
                            <path d="M9.5 13a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0z"/>
                        </svg>
                        <ul class="dropdown-menu" aria-labelledby="applicationsActions${i}">
                            <li><a href="#" data-title="${ localeTitle }" data-language="${ el.language }" data-id="${ el._id }" data-index="${ i }" class="dropdown-item po edit-locale text-dark d-flex justify-content-between align-items-center">${ __html('Edit') }<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square" viewBox="0 0 16 16"><path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/><path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/></svg></a></li>
                            <li><a href="#" data-title="${ localeTitle }" data-language="${ el.language }" data-id="${ el._id }" data-index="${ i }" class="dropdown-item d-none po sync-locale d-flex justify-content-between align-items-center" >${ __html('Sync') }<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-translate" viewBox="0 0 16 16"><path d="M4.545 6.714 4.11 8H3l1.862-5h1.284L8 8H6.833l-.435-1.286zm1.634-.736L5.5 3.956h-.049l-.679 2.022z"/><path d="M0 2a2 2 0 0 1 2-2h7a2 2 0 0 1 2 2v3h3a2 2 0 0 1 2 2v7a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2v-3H2a2 2 0 0 1-2-2zm2-1a1 1 0 0 0-1 1v7a1 1 0 0 0 1 1h7a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1zm7.138 9.995q.289.451.63.846c-.748.575-1.673 1.001-2.768 1.292.178.217.451.635.555.867 1.125-.359 2.08-.844 2.886-1.494.777.665 1.739 1.165 2.93 1.472.133-.254.414-.673.629-.89-1.125-.253-2.057-.694-2.82-1.284.681-.747 1.222-1.651 1.621-2.757H14V8h-3v1.047h.765c-.318.844-.74 1.546-1.272 2.13a6 6 0 0 1-.415-.492 2 2 0 0 1-.94.31"/></svg></a></li>
                            <li><hr class="dropdown-divider d-none-"></li>
                            <li><a href="#" data-id="${ el._id }" data-index="${ i }" class="dropdown-item po remove-locale text-danger d-flex justify-content-between align-items-center">${ __html('Remove') }<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16"><path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/><path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/></svg></a></li>
                        </ul>
                    </div>
                </td>
            </tr>`; 
        });

        if(response.locales.length){

            document.querySelector(".table tbody").innerHTML = rows;
        }else{

            document.querySelector(".table tbody").innerHTML = `<tr><td colspan="6">${ __html("Please add your first locale.") }</td></tr>`;
        }

        hideLoader();
    }

    /**
     * Initializes the event listeners for the component.
     */
    listeners = () => {

        // edit locale modal
        onClick('.edit-locale', this.editLocale);
        
        // edit locale modal
        onClick('.sync-locale', (e) => { e.preventDefault(); this.state.currentRow = e.currentTarget;  });

        // rename locale
        onClick('.remove-locale', this.removeLocale);

        // prevents listeners to be assigned twice
        if(!this.state.firstLoad) return;

        // add layout listener
        onClick('.btn-add', (e) => { e.preventDefault(); addLocaleModal(this); });
    }

    /**
     * Edits the locale. load all locale translations from the Kenzap Cloud.
     * Initalise locale editing modal
     *
     * @param {Event} e - The event object.
     * @returns {void}
     */
    editLocale = (e) => {

        this.state.currentRow = e.currentTarget;
        
        showLoader();
        
        this.state.ajaxCount = 0;

        // get user customized translations
        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    data: {
                        type:       'find', 
                        key:        'locale-' + this.state.ext,
                        id:         this.state.currentRow.dataset.id,
                        fields:     ['extension', 'content'],
                    },
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            // hide UI loader
            hideLoader();

            if(response.success){

                this.state.localesDB = response;

                this.shouldLocaleModal();

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });

        // get default locale
        this.getDefaultLocale('default');

        // get current language
        this.getDefaultLocale(this.state.currentRow.dataset.language);
    }
        
    /**
     * Removes a locale and its translations.
     * @param {Event} e - The event object.
     */
    removeLocale = (e) => {

        e.preventDefault();

        let c = confirm( __html('Completely remove this locale and translations?') );

        if(!c) return;

        // send data
        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    locale: {
                        type:       'delete',
                        key:        'locale-' + this.state.ext,   
                        id:         e.currentTarget.dataset.id,
                    }
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            if (response.success){

                toast( __html('Locale removed') );

                this.data();

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }

    /**
     * Adds a new translation row to the list.
     *
     * @param {Event} e - The event object.
     */
    addTransRow = (e) =>{

        let o = document.querySelector('.add-original').value, t = document.querySelector('.add-translated').value;

        if(o.length == 0) { alert( __html('Original text can not be empty') ); return; }
        if(t.length == 0) { alert( __html('Translated text can not be empty') ); return; }

        // check for repeated rows
        if(!document.querySelector('.edit-original[data-title="'+o+'"]')){

            let row = this.getLocalModalRow({id: e.currentTarget.id, original: o, translated: t, type: 'content' });
            document.querySelector('.add-row').insertAdjacentHTML('afterend', row);

            // reassign translation raw listener
            onClick('.remove-locale-text', this.removeTransRow);

            // clear current values
            document.querySelector('.add-original').value = "";
            document.querySelector('.add-translated').value = "";
        }else{

            alert( __html("Text is already in the list") );
        }

        setTimeout( () => document.querySelector(".add-original").focus(), 100);
    }

    /**
     * Removes a translation row from the DOM.
     *
     * @param {Event} e - The event object.
     * @returns {void}
     */
    removeTransRow = (e) =>{

        e.preventDefault();

        let c = confirm( __html('Remove this translation raw?') );

        if(!c) return;

        e.currentTarget.parentElement.parentElement.remove();
    
    }

    /**
     * Handles the event when searching for texts.
     * @param {Event} e - The event object.
     */
    searchTextsCont = (e) => {

        document.querySelector(".search-db").classList.remove("d-none");
    }

    /**
     * Searches for texts using the specified path and language.
     * @param {Event} e - The event object.
     */
    searchTexts = (e) => {

        let path = document.querySelector("#db-path").value;
        let from = document.querySelector("#source-language").value;
        let pa = path.split("/");

        if(path.length < 5) { alert( __html("Please specify valid path") ); return; }
        if(from.length < 1) { alert( __html("Please select language") ); return; }
        if(pa.length < 1) { alert( __html("Please specify valid path") ); return; }

        showLoader();

        // do API query
        fetch(getAPI()+'/translate/', {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    texts: {
                        type:       'find', 
                        key:        pa[0],
                        fields:     [pa[1]],
                        from:       from,
                        to:         this.state.currentRow.dataset.language, 
                        limit:      1000,
                    },
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){

                console.log(response);

                hideLoader();

                if(response.texts.length == 0){ alert( __html("Can not find available translation texts in the Cloud") ); return; }

                let ti = 0;
                response.texts.forEach((text, i) => {

                    // check for repeated rows
                    if(!document.querySelector('.edit-original[data-title="'+text.original+'"]')){

                        let row = this.getLocalModalRow({id: this.state.currentRow.dataset.id, original: text.original, translated: text.translated, type: 'content', bg: '#fff3cd'});
                        document.querySelector('.add-row').insertAdjacentHTML('afterend', row); 
                        ti++;
                    }else{
                        console.log("Skiping row entry");
                    }    
                });

                toast( __html('Adding %1$ translated texts', ti) );

                // reassign translation raw listener
                onClick('.remove-locale-text', this.removeTransRow);

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }
    
    /**
     * Generates a HTML string representing a table row for a local modal.
     * @param {Object} obj - The object containing the data for the table row.
     * @param {string} obj.bg - The background color of the table row.
     * @param {string} obj.original - The original text.
     * @param {string} obj.id - The ID of the locale.
     * @param {string} obj.translated - The translated text.
     * @param {string} obj.type - The type of the row.
     * @returns {string} - The HTML string representing the table row.
     */
    getLocalModalRow = (obj) =>{
    
        return `<tr ${ obj.bg ? 'style="background-color:'+obj.bg+'"' : "" }>
                    <td class="py-1">
                        <span class="edit-original" data-title="${ obj.original }" data-id="${ obj.id }">${ obj.original }</span>
                    </td>
                    <td class="py-1">
                        <textarea class="form-control edit-translated" data-original="${ obj.original }" data-type="${ obj.type }" rows="1">${ obj.translated }</textarea>
                    </td>
                    <td class="text-end">
                        <a href="#" data-id="${ obj.id }" title="${ __html('remove translation from the table') }" class="remove-locale-text text-danger me-2 ${ obj.type == 'extension' ? 'd-none' : '' }"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
                                <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                                <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                            </svg></a>
                    </td>
                </tr>`;
    }

    /**
     * Determines if the locale modal should be displayed.
     */
    shouldLocaleModal = () => {

        this.state.ajaxCount += 1; if(this.state.ajaxCount == 3) editLocaleModal(this);
    }

    /**
     * Loads the home structure.
     * 
     * @returns {void}
     */
    home = () => {

        if(!this.state.firstLoad) return;

        // get core html content 
        document.querySelector('#contents').innerHTML = HTMLContent(this, __);
    }
}

new EditLocale();