import { H, getAPI, __html, __attr, parseApiError, onClick, onChange, showLoader, hideLoader, toast } from '@kenzap/k-cloud';
import { languages, countries } from "../_/_helpers.js"

export const editLocaleModal = (_this) => {

    let modal = document.querySelector(".modal");
    let modalCont = new bootstrap.Modal(modal);
    
    modal.querySelector(".modal-dialog").classList.add('modal-fullscreen');
    modal.querySelector(".modal-title").innerHTML = _this.state.ext + ' / ' + _this.state.currentRow.dataset.title;

    let sourceLocaleList = ``, languagesList = ''; // <option value="" data-title="" data-language="" data-id="" >${__html('Choose locale')}</option>
    languages.forEach((lang) => { languagesList += `<option value="${ lang.code }" >${ lang.name }</option>`; });
 
    // render table rows
    _this.state.locales.forEach((el, i) => {
    
        let l = languages.filter((obj) => { return obj.code === el.language });
        let c = countries.filter((obj) => { return obj.code === el.location });
        let localeTitle = __html(l[0].name) + ( c[0] ? ' (' + __html(c[0].name) + ')' : '' );

        if(localeTitle == _this.state.currentRow.dataset.title) return;

        sourceLocaleList += `<option value="${ el._id }" data-title="${ localeTitle }" data-language="${ el.language }" data-id="${ el._id }" >${ localeTitle }${ el.location ? '<img class="ms-2 cc-flag" src="https://cdn.kenzap.com/flag/' + el.location.toLowerCase() + '.svg" alt="location footer flag">' : '' }</option>`;
    });

    let cached = '';

    let modalHTml = `

        <div class="row">
            <div class="col-sm-12">
                <div class="search-db border-start border-primary border-3 ps-3 my-2 d-none">
                    <div class="form-cont">
                        <div class="d-flex">
                            <div class="form-group mb-2 mt-1 me-3">
                                <label for="db-path" class="form-label">${ __html('Cloud storage key') }</label>
                                <input id="db-path" type="text" placeholder="${ __html('ecommerce-product/title') }" class="form-control " aria-label="${ __html('Search products') }" aria-describedby="inputGroup-sizing-sm" style="max-width: 400px;">
                                <p class="form-text">${ __html("Search cloud stored texts and add to translation list below.") }</p>
                            </div>
                            <div class="form-group mb-2 mt-1 me-3">
                                <label for="source-language" class="form-label">${ __html('Source language') }</label>
                                <select id="source-language" class="form-control" name="select-language">
                                    <option value="">${ __html('No language selected') }</option>
                                    <optgroup label="${ __html('Source language') }"> 
                                        ${ languagesList }
                                    </optgroup>
                                </select>
                                <p class="form-text">${ __html("Default language of original texts.") }</p>
                            </div>
                            <div class="form-group align-self-center mb-3 mt-0">
                                <button type="button" class="btn btn-primary btn-search-db btn-modal">${ __html("Search") }</button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="table-responsive">
                    <table class="table table-hover table-borderless align-middle table-striped table-t-list" style="min-width: 800px;">
                        <thead>
                            <tr>
                                <th>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="#212529" class="bi justify-content-end bi-search search-icon po mb-1 me-3" viewBox="0 0 16 16" >
                                        <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"></path>
                                    </svg>
                                    <div class="search-cont input-group input-group-sm mb-0 justify-content-start d-none">     
                                        <input type="text" placeholder="${ __html('Search products') }" class="form-control border-top-0 border-start-0 border-end-0 rounded-0" aria-label="${ __html('Search products') }" aria-describedby="inputGroup-sizing-sm" style="max-width: 200px;">
                                    </div>
                                    <span>${ __html("Original") }</span>
                                </th>
                                <th class="d-flex justify-content-between align-items-center">
                                    <div>${ __html("Translated") }</div>
                                    <div class="d-flex align-items-center">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-translate copy-locale-tray text-primary po me-2 " viewBox="0 0 16 16">
                                            <path d="M4.545 6.714 4.11 8H3l1.862-5h1.284L8 8H6.833l-.435-1.286zm1.634-.736L5.5 3.956h-.049l-.679 2.022z"/>
                                            <path d="M0 2a2 2 0 0 1 2-2h7a2 2 0 0 1 2 2v3h3a2 2 0 0 1 2 2v7a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2v-3H2a2 2 0 0 1-2-2zm2-1a1 1 0 0 0-1 1v7a1 1 0 0 0 1 1h7a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1zm7.138 9.995q.289.451.63.846c-.748.575-1.673 1.001-2.768 1.292.178.217.451.635.555.867 1.125-.359 2.08-.844 2.886-1.494.777.665 1.739 1.165 2.93 1.472.133-.254.414-.673.629-.89-1.125-.253-2.057-.694-2.82-1.284.681-.747 1.222-1.651 1.621-2.757H14V8h-3v1.047h.765c-.318.844-.74 1.546-1.272 2.13a6 6 0 0 1-.415-.492 2 2 0 0 1-.94.31"/>
                                        </svg>
                                        <select id="copy-locale" class="form-select form-select-sm copy-locale d-none" name="select-copy-locale">
                                            <option value="">${ __html('No locales selected') }</option>
                                            <optgroup label="${ __html('Available locales:') }">
                                                ${ sourceLocaleList }
                                            </optgroup>
                                        </select>
                                    </div>
                                </th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>`;

                        // header
                        let rows = `
                        <tr class="add-row">
                            <td class="py-1">
                                <textarea class="form-control add-original" data-original="${ cached }" rows="1"></textarea>
                            </td>
                            <td class="py-1">
                                <textarea class="form-control add-translated" data-translated="${ cached }" rows="1"></textarea>
                            </td>
                            <td class="text-end"> 
                                <a href="#" data-id="${ _this.state.currentRow.dataset.id }" title="${ __html('add translation to the table') }" class="add-trans-row text-success me-2"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-plus-square" viewBox="0 0 16 16">
                                <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z"/>
                                <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z"/>
                            </svg></a>
                            </td>
                        </tr>
                        `;

                        // populate table with content translation strings
                        let texts = _this.state.localesDB.data.content;
                        if (_this.state.localesDB.data.content) Object.keys(texts).forEach((text) => { rows += _this.getLocalModalRow({id: _this.state.currentRow.dataset.id, original: text, translated: texts[text], type: 'content' }) }); 
                        
                        // popoulate table modal rows with translation texts provided by extension
                        let def = _this.state.locales[_this.state.currentRow.dataset.language] ? _this.state.locales[_this.state.currentRow.dataset.language] : _this.state.locales.default;
                        if(def == '404: Not Found') def = _this.state.locales.default;

                        texts = {...JSON.parse(def).texts, ..._this.state.localesDB.data.extension};
                        Object.keys(texts).forEach((text) => { rows += _this.getLocalModalRow({id: _this.state.currentRow.dataset.id, original: text, translated: texts[text], type: 'extension' }) }); 
                    
                        modalHTml += rows;

            modalHTml += `</tbody>
                    </table>
                </div>
            </div>
        </div>`;

    // add generated HTML code to the modal body 
    modal.querySelector(".modal-body").innerHTML = modalHTml;

    // modal footer buttons
    modal.querySelector(".modal-footer").innerHTML = `
        <button type="button" class="btn btn-primary btn-locale-save btn-modal">${ __html('Save') }</button>
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">${ __html('Cancel') }</button>
    `;

    // view localization tray
    onClick('.copy-locale-tray', (e) => {

        document.querySelector('.copy-locale').classList.toggle('d-none');
    });

    // copy and localise texts
    onChange('.copy-locale', (e) => {

        if(!e.target.selectedIndex) return;

        let locale = { id: e.target.value, from: { name: e.target.options[e.target.selectedIndex].innerHTML }, to: { name: _this.state.currentRow.dataset.title }};

        if(!confirm(__("Copy locale texts from %1$ and translate to %2$?", e.target.options[e.target.selectedIndex].innerHTML, _this.state.currentRow.dataset.title))){ e.preventDefault(); return false; }

        showLoader();

        let override = confirm(__("Override existing translations?"));

        _this.state.ajaxCount = 0;

        // get user customized translations
        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    data: {
                        type:       'find', 
                        key:        'locale-' + _this.state.ext,
                        id:         e.target.value,
                        fields:     ['extension', 'content'],
                    },
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){

                response.data.content = response.data.content ? response.data.content : {};

                // response.data.content = {...response.data.content, ...response.data.extension};
                // console.log(response.data.content);
                // console.log("---------------");
                // console.log(response.data.extension);
                // // return;
                const chunk_size = 50

                let content = {};

                if(override){

                    // translate all records
                    content = {...response.data.content, ...response.data.extension};

                }else{

                    // skip records already that are present in the table
                    Object.keys(response.data.content).forEach((key, i) => {

                        if (!document.querySelector('.edit-original[data-title="'+key+'"]')) content[key] = response.data.content[key];
                    }); 
                }

                // Split response.data.content into chunks
                const chunks = [];
                for (let i = 0; i < Object.keys(content).length; i += chunk_size) {

                    const chunk = Object.keys(content).slice(i, i + chunk_size).reduce((obj, key) => {
                        obj[key] = content[key];
                        return obj;
                    }, {});

                    chunks.push(chunk);
                }

                console.log(chunks);    
                // return;

                if(chunks.length){ showLoader(); }else{ hideLoader(); }
                
                // Process each chunk
                chunks.forEach((chunk, index) => {

                    _this.state.ajaxCount += 1;
                    setTimeout(() => {
                        fetch(getAPI()+'/translate/', {
                            method: 'post',
                            headers: H(),
                            body: JSON.stringify({
                                query: {
                                    localize: {
                                        type: 'localize', 
                                        locale: locale,
                                        texts: chunk,
                                    },
                                }
                            })
                        })
                        .then(response => response.json())
                        .then(response => {

                            _this.state.ajaxCount -= 1;

                            if (response.success) {

                                if (response.localize.texts.length == 0) {

                                    hideLoader();
                                    alert(__html("Can not find available translation texts in the Cloud"));
                                    return;
                                }

                                let ti = 0;
                                response.localize.texts.forEach((text, i) => {

                                    // override existing translations
                                    if(override){

                                        if(document.querySelector('.edit-translated[data-original="'+text.original+'"]')){ document.querySelector('.edit-translated[data-original="'+text.original+'"]').value = text.translated; ti+=1; }
                                    }

                                    // check for repeated rows
                                    if (!document.querySelector('.edit-original[data-title="'+text.original+'"]')) {
                                        let row = _this.getLocalModalRow({id: _this.state.currentRow.dataset.id, original: text.original, translated: text.translated, type: 'content', bg: '#fff3cd'});
                                        document.querySelector('.add-row').insertAdjacentHTML('afterend', row); 
                                        ti++;
                                    } else {
                                        console.log("Skipping row entry");
                                    }    
                                });

                                toast(__html('Adding %1$ translated texts', ti));

                                if(_this.state.ajaxCount<=0) hideLoader();
                                
                                // reassign translation raw listener
                                onClick('.remove-locale-text', _this.removeTransRow);
                            } else {
                                parseApiError(response);
                            }
                        })
                        .catch(error => {
                            console.error('Error:', error);
                        });
                    }, index * 7000); // Delay each chunk by 1 second
                });

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });

    });

    // add translation row on click listener
    onClick('.add-trans-row', _this.addTransRow);

    // remove translation raw listener
    onClick('.remove-locale-text', _this.removeTransRow);

    // search texts in cloud db 
    onClick('.btn-search-db', _this.searchTexts);

    // call search cloud texts feature
    onClick('.search-icon', _this.searchTextsCont);

    // modal button save listener
    onClick('.btn-locale-save', (e) => {

        e.preventDefault();

        let data = {};

        data['extension'] = {};
        data['content'] = {};

        // get all translation strings
        for(let trans of modal.querySelectorAll(".edit-translated")){

            data[trans.dataset.type][trans.dataset.original] = trans.value;
        }

        // send data
        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    locale: {
                        type:       'update',
                        key:        'locale-' + _this.state.ext,   
                        id:         _this.state.currentRow.dataset.id,
                        data:       data
                    }
                }
            }) 
        })
        .then(response => response.json())
        .then(response => {

            if (response.success){

                modalCont.hide();

                _this.data();

            }else{

                parseApiError(response);
            }
            
            console.log('Success:', response);
        })
        .catch(error => {
            console.error('Error:', error);
        });   
    });

    modalCont.show();
}